<!--  -->
<template>
  <div class="AddUnion_father"  @click="hideDiv">
      <div class="submit_c" >
            <div class="PlayerRanking_header">
                <img @click.stop="toReturn" :src="staticImgH+'zuojiantou.png'" alt="">
                <span></span>
                 <span v-if="disabledXiu" @click="editYes"></span>
                 <span v-else @click="saveYes"></span>
            </div>
            <div class="AddUnion_Info" v-if="guildData.is_show==1">
                    <ul class="AddUnion_InfoList">
                        <li>
                            <div class="Info_name"><span></span>*</div>
                            <input type="text" :disabled='true' v-model="nickname" placeholder="">
                        </li>
                        <li>
                            <div class="Info_name"><span></span>*</div>
                            <input type="text" :disabled='disabledXiu' maxlength="11" oninput = "value=value.replace(/[^\d]/g,'')" v-model="phone" placeholder="">
                        </li>
                        <li>
                            <div class="Info_name"><span></span>*</div>
                            <input type="text" :disabled='disabledXiu' oninput ="value=value.replace(/[^\w_]/g,'')" v-model="wechatId" placeholder="hsdjjkdo">
                        </li>
                        <li>
                            <div class="Info_name"><span></span>*</div>
                            <div class="form-groupWrap">
                                    <div class="form-group">
                                        <ul class="upload-imgs">
                                        <li :class="['uploadimgs_first',{'uploadimgs_opacity':uploadimgsOpacity}]">
                                            <input :disabled='true' type="file" class="upload" id="upload_img" @change="addImg" ref="inputer" multiple accept="image/*"/>
                                            <a class="add"><span></span><span></span></a>
                                        </li>
                                            <li>
                                            <p class="img"><img  :src="img">
                                            <!-- <a class="close" @click="delImg(key)"></a> -->
                                            </p>
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="form-group">
                                        <ul class="upload-imgs">
                                        <li :class="['uploadimgs_first',{'uploadimgs_opacity':uploadimgsOpacityTwo}]">
                                            <input :disabled='true' type="file" class="upload" id="upload_img" @change="addImgTwo" ref="inputerTwo" multiple accept="image/*"/>
                                            <a class="add"><span></span><span></span></a>
                                        </li>
                                            <li>
                                            <p class="img"><img  :src="imgTwo">
                                            <!-- <a class="close" @click="delImg(key)"></a> -->
                                            </p>
                                            </li>
                                        </ul>
                                    </div>
                            </div>
                            <!-- <input type="text" maxlength="18" oninput="value=value.replace(/[\W]/g,'')" @change="cardNumber" v-model="isNumber" placeholder=""> -->
                        </li>
                        <li>
                            <div class="Info_name"><span></span></div>
                            <ul class="sex">
                                <li @click.stop="sexSelectC(item.id)" v-for="(item,index) in sexData" :key="index"> <img :src="item.id==sexImgIndex?staticImgH+'noselYes.png':staticImgH+'nosel.png'" alt=""> <span>{{item.name}}</span> </li>
                            </ul>
                        </li>
                        <li>
                            <div class="Info_name"><span></span>*</div>
                            <input type="text" :disabled='disabledXiu' oninput = "value=value.replace(/[^\d]/g,'')" v-model="age" placeholder="">
                        </li>
                         <li>
                            <div class="Info_name"><span></span></div>
                            <input type="text" :disabled='disabledXiu' v-model="heightVal" oninput = "value=value.replace(/[^\d]/g,'')" placeholder="">
                        </li>
                        <li>
                            <div class="Info_name"><span></span></div>
                            <input type="text" :disabled='disabledXiu' v-model="weightVal" oninput = "value=value.replace(/[^\d]/g,'')" placeholder="">
                        </li>
                        <li>
                            <div class="Info_name"><span></span></div>
                            <input type="text" :disabled='disabledXiu' v-model="constellationVal"   placeholder="">
                        </li>
                        <li>
                            <div class="Info_name"><span></span></div>
                            <input type="text" :disabled='disabledXiu' v-model="SpecialtyVal"  placeholder="">
                        </li>
                        <li>
                            <div class="Info_name"><span></span></div>
                            <input type="text" :disabled='disabledXiu' v-model="CareerVal"  placeholder="">
                        </li>
                        <li>
                            <div class="Info_name"><span></span>*</div>
                            <textarea v-model="SignVal" :disabled='disabledXiu' placeholder="" name="" id="">

                            </textarea>
                        </li>
                        <!-- <li>
                            <div class="Info_name"><span></span>*</div>
                            <input type="text" :disabled='disabledXiu' maxlength="18" oninput="value=value.replace(/[\W]/g,'')" @change="cardNumber" v-model="isNumber" placeholder="">
                        </li> -->
                        
                        
                        <!--  -->
                        <li>
                            <div class="Info_whether">
                                <div class="Info_whethername"><span></span>*</div>
                                <div :class="[{'setAddressBtnOne':WisDefault==0},{'setAddressBtnTwo':WisDefault==1}]" @click.stop="whetherBtnDefa"><span></span></div>
                            </div>
                            <ul class="Info_platform"  v-if="platformShow">
                                <li @click.stop="platfromSelC(index,item)" v-for="(item,index) in LivePlatformData" :key="index"> <img :src="index==platformImgIndex?staticImgH+'noselYes.png':staticImgH+'nosel.png'" alt=""> <span>{{item}}</span> </li>
                            </ul>
                            <div  class="Info_other" v-if="platformShow">
                                <div class="Info_otherRight" @click.stop="platformotherC">
                                    <img :src="platformotherImgIndex?staticImgH+'noselYes.png':staticImgH+'nosel.png'" alt=""> 
                                    <span></span>
                                </div>
                                <input :disabled='disabledXiu' v-if="showplatformotherinput" @change="zhiboChange" v-model="inputSoftware" type="text" placeholder="">
                            </div>
                            <input :disabled='disabledXiu' v-if="platformShow" type="text" v-model="softId" placeholder="id">
                        </li>
                        <!--  -->
                        <li>
                            <div class="Info_whether">
                                <div  class="Info_whethername"><span> </span>*</div>
                                <div :class="[{'setAddressBtnOne':DisDefault==0},{'setAddressBtnTwo':DisDefault==1}]" @click.stop="DuanBtnDefa"><span></span></div>
                            </div>
                            <ul class="Info_platform" v-if="duanShow">
                                <li @click.stop="shortVideoSelC(index,item)" v-for="(item,index) in ShortVideoData" :key="index"> <img :src="index==shortVodeoImgIndex?staticImgH+'noselYes.png':staticImgH+'nosel.png'" alt=""> <span>{{item}}</span> </li>
                            </ul>
                            <div class="Info_other" v-if="duanShow"><div class="Info_otherRight" @click.stop="shortVodeootherC"><img :src="shortVodeootherImgIndex?staticImgH+'noselYes.png':staticImgH+'nosel.png'" alt=""> <span></span></div>
                            <input :disabled='disabledXiu' v-model="inputduanValue" @change="videoChange" v-if="showshortVodeootherinput" type="text" placeholder=""></div>
                            <input :disabled='disabledXiu' type="text" v-if="duanShow" v-model="videoId" placeholder="id">
                        </li>
                        <li>
                            <div class="Info_whether">
                                <div  class="Info_whethername"><span> </span>*</div>
                                <div :class="[{'setAddressBtnOne':WangisDefault==0},{'setAddressBtnTwo':WangisDefault==1}]" @click.stop="WangBtnDefa"><span></span></div>
                            </div>
                            <div class="onlineShop_wrap" v-if="isWang">
                                <div class="onlineShop" @click.stop="wangNameC"><span>{{shopNames}}</span> <img :src="staticImgH+'nojian.png'" alt=""></div> <input :disabled='disabledXiu' v-model="isShop" type="text" placeholder="">
                                <ul  v-if="onlineShopListxShow" >
                                    <li @click.stop="shopNameC(item.name)" v-for="(item,index) in onlineShopData"  :key="index">
                                        {{item.name}}
                                    </li>
                                </ul>
                            </div>
                        </li>
                    </ul>
            </div>
            <div v-if="guildData.is_show==0"></div>
            <div v-if="guildData.is_show==2"></div>
            <!-- <div class="ImmAdd" @click.stop="ImmAddC"></div> -->
        </div>
      <!--  -->
         <transition name="fade">
            <div class="promptFather" v-if="showPrompt">
                <div class="prompt" >
                    {{promptContent}}
                </div>
            </div>
        </transition>
  </div>
</template>
<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import qs from 'qs'
export default {
  data () {
    return {
        WisDefault:0,
        DisDefault:0,
        WangisDefault:0,
        sexSelImg:'nosel.png',
        disabledXiu:true,//
        sexData:[
            {
                name:'',
                id:2,
            }
            ,
            {
                name:'',
                id:1
            }
        ],
        sexImgIndex:2,
        LivePlatformData:[
            '','YY','','','','','',''
        ],
        platformImgIndex:-1,
        platformotherImgIndex:false,
        showplatformotherinput:false,
        platformShow:false,
        ShortVideoData:['',''
        ],
        shortVodeoImgIndex:-1,
        shortVodeootherImgIndex:false,
        showshortVodeootherinput:false,
        duanShow:false,
        
        onlineShopData:[
            {
                name:''
            },{
                name:''
            },{
                name:''
            }
        ],
        isWang:false,
        onlineShopListxShow:false,


        nickname:'', //
        phone:'',  //
        wechatId:'',  //
        // isNumber:'',//
        sex:2,//
        age:'',//
        software:'',  //
        inputSoftware:'', //
        softId:'', //
        isVideo:'',//
        inputduanValue:'', //  
        videoId:'', //
        shopName:'',  //
        shopNames:'',  //
        isShop:'',  //
        heightVal:'',//
        weightVal:'',//
        constellationVal:'',//
        SpecialtyVal:'',//
        CareerVal:'',//
        SignVal:'',//


        // 
        promptContent:'', //
        showPrompt:false,//
        
        formData:new FormData(), 
        uploadimgsOpacity:false,  //  
        img:'',//
        uploadimgsOpacityTwo:false,// 
        imgTwo:'',//

        guildData:'',
    };
  },
//   components: {},
    computed:{
        ...mapState(['staticImgH','tokenH'])
    },
    mounted(){
            var obj=qs.stringify({
                type:1
            })
            this.$http.post('api/user/me_gong',obj,{
                headers: {
                    'authorization': this.tokenH
                }
            }).then((res)=>{
                    if(res.data.code==200){
                            var data=res.data.data.result
                            this.guildData=data
                            this.nickname=data.nickname
                            this.phone=data.phone
                            this.wechatId=data.wechat_id
                            this.isNumber=data.is_number
                            this.sex=data.sex
                            this.age=data.age
                            this.sexImgIndex=data.sex
                            this.heightVal=data.height1
                            this.weightVal=data.weight1
                            this.constellationVal=data.constellation1
                            this.SpecialtyVal=data.strong
                            this.CareerVal=data.datacareer
                            this.SignVal=data.signature1
                            this.img=data.just_number
                            this.imgTwo=data.over_number
                            this.uploadimgsOpacity=true
                            this.uploadimgsOpacityTwo=true
                            if(data.software){
                                this.WisDefault=1 
                                this.platformShow=true
                                this.softId=data.soft_id
                                if(this.LivePlatformData.indexOf(data.software)!==-1){
                                    this.platformImgIndex=this.LivePlatformData.indexOf(data.software)
                                    this.software=data.software
                                }else {
                                    this.platformotherImgIndex=true;
                                    this.showplatformotherinput=true
                                    this.inputSoftware=data.software
                                }
                            }
                            if(data.is_video){
                                this.DisDefault=1
                                this.duanShow=true
                                this.videoId=data.video_id
                                if(this.ShortVideoData.indexOf(data.is_video)!==-1){
                                    this.shortVodeoImgIndex=this.ShortVideoData.indexOf(data.is_video)
                                    this.isVideo=data.is_video
                                }else {
                                    this.shortVodeootherImgIndex=true;
                                    this.showshortVodeootherinput=true
                                    this.inputduanValue=data.is_video
                                }
                            }
                            if(data.shop_name){
                                this.WangisDefault=1
                                this.isWang=true
                                this.shopNames=data.shop_name
                                this.shopName=data.shop_name
                                this.isShop=data.is_shop
                            }else{
                                this.shopNames=data.shop_name
                                this.shopName=data.shop_name
                            }
                    }else{
                        this.alertText(res.data.msg)
                    }
            })
    },
    methods: {
            //  1
                    addImg(event){
                        if(event.target.files[0]){
                            this.uploadimgsOpacity=true
                            var size = Math.floor(event.target.files[0].size / 1024);
                        }else{
                            return false
                        }
                        if (size > 1*1024*1024) {
                            alert('1M');
                            return false
                        }
                        var objurl = this.getObjectURL(event.target.files[0])
                        this.img=objurl
                    },
            //  2
                    getObjectURL(file) {  
                        var url = null ;   
                        //  js   
                        if (window.createObjectURL!=undefined) { // basic  
                            url = window.createObjectURL(file) ;  
                        } else if (window.URL!=undefined) { // mozilla(firefox)  
                            url = window.URL.createObjectURL(file) ;  
                        } else if (window.webkitURL!=undefined) { // webkit or chrome  
                            url = window.webkitURL.createObjectURL(file) ;  
                        }  
                        return url ; 
                    },
            //  1
                    addImgTwo(event){
                        if(event.target.files[0]){
                            this.uploadimgsOpacityTwo=true
                            var size = Math.floor(event.target.files[0].size / 1024);
                        }else{
                            return false
                        }
                        if (size > 1*1024*1024) {
                            alert('1M');
                            return false
                        }
                        var objurl = this.getObjectURL(event.target.files[0])
                        this.imgTwo=objurl
                    },
            //   
                    saveYes(){
                        if(this.shopName=='' && this.isShop!==''){
                            this.shopName=this.shopNames
                        }
                        var obj=qs.stringify({
                                type:2,
                                nickname:this.nickname,
                                phone:this.phone,
                                wechat_id:this.wechatId,
                                // is_number:this.isNumber,
                                sex:this.sex,
                                age:this.age,
                                software:this.software,
                                soft_id:this.softId,
                                is_video:this.isVideo,
                                video_id:this.videoId,
                                shop_name:this.shopName,
                                is_shop:this.isShop,
                                signature1:this.SignVal,//
                                weight1:this.weightVal,
                                height1:this.heightVal,
                                constellation1:this.constellationVal,
                                strong:this.SpecialtyVal,
                                career:this.CareerVal
                        })
                        this.$http.post('api/user/me_gong',obj,{
                            headers: {
                                'authorization': this.tokenH
                            }
                        }).then((res)=>{
                            if(res.data.code==200){
                                    this.alertText('')
                                    this.disabledXiu=true
                            }else{
                                    this.alertText(res.data.msg)
                            }
                        })
                    },
            //   
                    editYes(){
                        this.disabledXiu=false
                    },
            //      
                    hideDiv(){
                        this.onlineShopListxShow=false
                    },
            // 
                    alertText(text){
                        var self=this
                            clearInterval(self.timer2);
                                this.promptContent=text
                                this.showPrompt=true
                                self.timer2=setTimeout(function(){
                                        self.showPrompt=false
                                        clearInterval(self.timer2);
                                },2000)
                            return false;
                    },
            // 
                    cardNumber(){
                        var idCardReg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
                        iFlag = idCardReg.test(this.idNumber);
                        if(!iFlag){
                            this.alertText('')
                        }
                    },
            // 
                    wangNameC(){
                        if(!this.disabledXiu){
                            this.onlineShopListxShow=true
                        }
                    },
            //   
                    shopNameC(name){
                        if(!this.disabledXiu){
                            this.shopName=name
                            this.shopNames=name
                            this.onlineShopListxShow=false
                        }
                    },
            // input
                    videoChange(){
                        this.isVideo=this.inputduanValue
                    },
            // input
                    zhiboChange(){
                        this.software=this.inputSoftware
                    },
            //   
                    sexSelectC(id){
                        if(!this.disabledXiu){
                                this.sexImgIndex=id
                                this.sex=id
                        }
                    },
            // 
                    whetherBtnDefa(){
                        if(!this.disabledXiu){
                                this.WisDefault==0?this.WisDefault=1:this.WisDefault=0  //
                                if(this.WisDefault==0){ //
                                    this.platformShow=false  // id 
                                }else if(this.WisDefault==1){ //
                                    this.platformShow=true  // id 
                                }
                                this.platformImgIndex=-1  //
                                this.platformotherImgIndex=false  // 
                                this.showplatformotherinput=false // input
                                this.inputSoftware=''  //input 
                                this.softId=''  //id
                                this.software=''  //value
                        }
                    },
            // 
                    DuanBtnDefa(){
                        if(!this.disabledXiu){
                                this.DisDefault==0?this.DisDefault=1:this.DisDefault=0 //
                                if(this.DisDefault==0){//
                                    this.duanShow=false  // id 
                                }else if(this.DisDefault==1){//
                                    this.duanShow=true   // id 
                                }
                                this.shortVodeoImgIndex=-1  //
                                this.shortVodeootherImgIndex=false // 
                                this.showshortVodeootherinput=false   // input 
                                this.inputduanValue=''  //  input 
                                this.videoId=''  //id
                                this.isVideo=''  //
                        }
                        
                    },
            // 
                        WangBtnDefa(){
                            if(!this.disabledXiu){
                                    this.WangisDefault==0?this.WangisDefault=1:this.WangisDefault=0  //
                                    if(this.WangisDefault==0){//
                                        this.isWang=false 
                                    }else if(this.WangisDefault==1){//
                                        this.isWang=true
                                    }
                                    this.shopNames='',  //
                                    this.shopName=''   // ''
                                    this.isShop=''  //input value
                            }
                        },
            //   
                    platfromSelC(index,name){
                        if(!this.disabledXiu){
                                this.platformImgIndex=index  //
                                this.software=name   //
                                this.platformotherImgIndex=false  //
                                this.showplatformotherinput=false //input''
                            }
                        
                    },
            //  
                    shortVideoSelC(index,name){
                        if(!this.disabledXiu){
                                this.shortVodeoImgIndex=index
                                this.isVideo=name
                                this.shortVodeootherImgIndex=false
                                this.showshortVodeootherinput=false
                        }
                    },
            // 
                    platformotherC(){
                        if(!this.disabledXiu){
                                this.platformotherImgIndex=!this.platformotherImgIndex // 
                                this.platformImgIndex=-1; //
                                this.software='';    // 
                                this.inputSoftware=''  //input 
                                if(this.platformotherImgIndex==true){ // 
                                    this.showplatformotherinput=true  //input
                                }else{ // 
                                    this.showplatformotherinput=false  ////input
                                }
                        }
                    },
            // 
                shortVodeootherC(){
                    if(!this.disabledXiu){
                            this.shortVodeootherImgIndex=!this.shortVodeootherImgIndex
                            this.shortVodeoImgIndex=-1
                            this.isVideo=''
                            this.inputduanValue=''
                            if(this.shortVodeootherImgIndex==true){
                                this.showshortVodeootherinput=true
                            }else{
                                this.showshortVodeootherinput=false
                            }
                    }
                },
                toReturn(){
                    this.$router.push('/Mine')
                },
    }
}

</script>
<style scoped lang="stylus">
.AddUnion_father{
    letter-spacing :0.04rem;
}
.PlayerRanking_header{
    width:100%
    height:1.23rem;
    display :flex;
    align-items :center;
    justify-content :space-between;
    position :relative;
    padding:0 0.4rem;
    >img{
        width:0.32rem;
        height:0.56rem;
    }
    >span{
        font-size:0.48rem;
        color:rgba(0, 0, 0, 1);
    }
}
.AddUnion_Info{
    margin:0.4rem;
    padding:0.32rem 0.32rem;
    border-radius:0.133rem;
    box-shadow :0 0.05rem 0.3rem rgba(254, 222, 227, 1);
}

.setAddressBtnOne{
        width:1.253rem;
        height:0.48rem;
        background:rgba(0, 0, 0, 0.1);
        display :flex;
        align-items :center;
        border-radius:3.5rem;
        margin-right:0.27rem;
        >span{
            width:0.373rem;
            height:0.373rem;
            background :rgba(255, 255, 255, 1);
            margin-left:0.13rem;
            border-radius:50%;
             transform :translateX(0);
            transition :all 0.5s;
            color:rgba(0, 0, 0, 0.38);
            font-size:0.27rem;
            line-height :0.373rem;
            text-align:center;
        }
    }
     .setAddressBtnTwo{
       width:1.253rem;
        height:0.48rem;
        background:rgba(255, 193, 203, 1);
        display :flex;
        align-items :center;
        border-radius:3.5rem;
        margin-right:0.27rem;
        >span{
            width:0.43rem;
            height:0.43rem;
            background :#fff;
            margin-left:0.13rem;
            border-radius:50%;
            transform :translateX(0.59rem);
            transition :all 0.5s;
        }
}
.AddUnion_InfoList{
    >li{
        margin-top:0.32rem;
        display :flex;
        flex-direction :column;
        >.Info_name{
            font-size:0.37rem;
            color:#FF0000;
            margin-bottom:0.27rem;
            >span{
                color:#000000;
            }
        }
        >textarea{
            width:100%;
            height:1.867rem;
            border-radius:0.16rem;
            border:0.03rem solid rgba(0, 0, 0, 0.38);
            outline:none;
        }
        >input{
            width:100%;
            height:0.67rem;
            border:0.02rem solid #707070;
            outline:none;
            border-radius:0.08rem;
            letter-spacing :0.04rem;
            padding-left:0.16rem;
            &::-webkit-input-placeholder { 
                font-size:0.32rem;
			    color: rgba(0, 0, 0, 0.38);
            }
            &::-moz-placeholder { 
                 font-size:0.32rem; 
                color: rgba(0, 0, 0, 0.38);
            }
            &:-ms-input-placeholder {
                 font-size:0.32rem;
                color:rgba(0, 0, 0, 0.38);
            }
            &:-moz-placeholder {
                 font-size:0.32rem;
                color: rgba(0, 0, 0, 0.38);
            }
        }
        >.sex{
            display :flex;
            margin-left:0.27rem;
            >li{
                display :flex;
                align-items :center;
                margin-right:1.2rem;
                >img{
                    width:0.24rem;
                    height:0.24rem;
                    margin-right:0.3rem;
                    background :pink;
                    border-radius:50%;
                }
                >span{
                    font-size:0.32rem;
                    color:rgba(0, 0, 0, 0.38)
                    
                }
                >selSex{
                    color:#000;
                }
            }
        }
        >.Info_whether{
            display :flex;
            justify-content :space-between;
            .Info_whethername{
                font-size:0.37rem;
                color:#FF0000;
                margin-bottom:0.27rem;
                >span{
                    color:#000000;
                }
            }
        }
        >.Info_platform{

            display :flex;
            flex-wrap:wrap;
            margin-top:0.27rem;
            margin-left:0.27rem;
           >li{
                display :flex;
                align-items :center;
                margin-right:0.7rem;
                >img{
                    width:0.24rem;
                    height:0.24rem;
                    margin-right:0.18rem;
                    background :pink;
                    border-radius:50%;
                }
                >span{
                    font-size:0.32rem;
                    color:rgba(0, 0, 0, 0.38)
                    
                }
                >selSex{
                    color:#000;
                }
            }
        }
        >.Info_other{
            display:flex;
             margin-top:0.2rem;
            margin-left:0.27rem;
            margin-bottom:0.27rem;
            >.Info_otherRight{
                display :flex;
                align-items :center;
                margin-right:0.3rem;
                >img{
                    width:0.24rem;
                    height:0.24rem;
                    margin-right:0.18rem;
                    background :pink;
                    border-radius:50%;
                }
                >span{
                    font-size:0.32rem;
                    color:rgba(0, 0, 0, 0.38)
                    
                }
                >selSex{
