        }
    }
}
.ImmAdd{
    width:9.2rem;
    height:1.07rem;
    background :rgba(255, 193, 203, 1);
    font-size:0.347rem;
    color:rgba(255, 255, 255, 1);
    line-height :1.07rem;
    text-align :center;
    margin:0.4rem auto;
}

// 
.promptFather{
    width:100%;
    position :absolute;
    bottom:50%;
    left:0;
    z-index:10;
    display :flex;
    justify-content :center;
    align-items :center;
    >.prompt{
        padding:0.15rem 0.3rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.32rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}
// 
.form-groupWrap{
    display:flex;
}
.form-group{
    width:2.4rem;
    height:2.4rem;
    margin-right:0.27rem;
    display:inline-block;
    &:last-child{
        margin-right:0;
    }
}
.upload-imgs{overflow: hidden;font-size: 0;position :relative;}
.uploadimgs_first{
  position :absolute;
  top:0;
  left:0;
  z-index:900;
}
.uploadimgs_opacity{
  opacity :0;
}
.upload-imgs li{
    width: 2.4rem;
    height: 2.4rem;
    font-size: 0.373rem;
    display: flex;
    align-items :center;
    justify-content :center;
    margin-right: 0.67rem;
    border: 0.053rem dashed rgba(0, 0, 0, 0.15);
    text-align: center;
    vertical-align: middle;
}

.upload-imgs .add{
    display: flex;
    flex-direction :column;
    align-items :center;
    justify-content :center;
    background-color: rgba(0, 0, 0, 0.1);
    color: rgba(0, 0, 0, 0.38);
    width:2.053rem;
    height: 2.053rem;
    padding: 0.213rem 0;
    font-size:0.32rem;
    >span{
        line-height:0.4rem;
    }
}
.upload-imgs .add span{

}
.upload-imgs li .upload{opacity :0;position: absolute;top: 0;bottom: 0;left: 0;right: 0;width: 2.4rem;height:2.4rem;}
.upload-imgs .img img{width:100%;vertical-align: middle;}
.upload-imgs .img .close{display: none;}
.upload-imgs li:hover .img .close{display: block;position: absolute;right: -0.16rem;top: -0.27rem;line-height: 1rem;font-size: 0.6rem;}
</style>



