<!--  -->
<template>
  <div class="comOrder">
       <!--  -->
      <div class="hideOrder"></div>
      <div class="myOrderListHeader">
                <img @click="toMime" class="myOrderListHeaderLeft" :src="staticImgH+'zuojiantou.png'" alt="">
                <span></span>
                <span></span>
            </div>
      <div class="comOrder_info">
          <div class="comOrder_infoTitle"></div>
          <div class="comOrder_infoNum">{{comOrder.orderSn}}</div>
          <div class="comOrder_infoMoney"><span>{{comOrder.orderAmount}}</span></div>
      </div>
      <div class="comOrder_body">
          <div class="comOrder_bodyTitle"></div>
          <div class="comOrder_bodyDes" @click="getPayPermit">
              <div class="comOrder_bodyDesTop"><img :src="staticImgH+'weiPay.png'" alt=""><span></span></div>
              <img :src="staticImgH+'weiSel.png'" alt="">
            </div>
      </div>
      <!--  -->
            <transition name="fade">
                <div class="promptFather" v-if="showPrompt">
                    <div class="prompt" >
                        {{promptContent}}
                    </div>
                </div>
            </transition>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
var qs = require('qs');
export default {
  data () {
    return {
        dataResult:'',//
        // 
        promptContent:'', //
        showPrompt:false,//
        timer2:'',
    };
  },

//   components: {},

  computed:{
        ...mapState(['comOrder','staticImgH'])
    },

//   mounted: {},

  methods: {
      toMime(){
          this.$router.push('/orderList')
      },
      //  wx.config
            getPayPermit(){
                var PayPermitObj=qs.stringify({
                    order_sn:this.comOrder.orderSn,
                    trade_type:'JSAPI',
                })
                this.$http.post('api/wechat/dopay',PayPermitObj).then((res)=>{
                        if(res.data.code==200){
                            var data=res.data.data
                            this.dataResult=data //
                            var vm=this
                            wx.config({
                                debug: false, // ,apialertpclogpc
                                appId: data.appId, // 
                                timestamp:data.timeStamp, // 
                                nonceStr: data.nonceStr, // 
                                signature: data.sign,// 
                                jsApiList: ['chooseWXPay'] // JS
                            });
                            this.payLe()
                        }else{
                            var self=this
                            clearInterval(self.timer2);
                                    this.promptContent=res.data.msg
                                    this.showPrompt=true
                                    self.timer2=setTimeout(function(){
                                        self.showPrompt=false
                                        clearInterval(self.timer2);
                                    },2000)
                                return false;
                        }
                })
            },
        //  wx.chooseWXPay
            payLe(){
                    var vm=this
                    wx.ready(function(){
                        wx.chooseWXPay({
                            timestamp: vm.dataResult.timeStamp, // jssdktimestamptimeStampS
                            nonceStr: vm.dataResult.nonceStr, //  32 
                            package: vm.dataResult.packag, // prepay_idprepay_id=\*\*\*
                            signType: 'MD5', // 'SHA1''MD5'
                            paySign:  vm.dataResult.sign, // 
                            success: function (res) {
                                this.myOrderListPages('/Mine')  //
                                vm.orderTypes('WAITSEND')  //orderType
                                vm.orderNums(2)   //
                                vm.$router.push('/orderList')  //
                            },
                            fail(){
                                alert('')
                            }
                        });
                    }) 
            },
            ...mapMutations(['myOrderListPages','orderTypes','orderNums','comOrders']),
  }
}

</script>
<style scoped lang='stylus'>
.hideOrder{
    width:100%;
    height:2.06rem;
}
.myOrderListHeader{
    width:100%;
    height:1.23rem;
    background :#fff;
    display :flex;
    justify-content :space-between;
    align-items :center;
    padding:0 0.27rem;
    border-bottom:0.03rem solid #F1F1F1;
    position:fixed;
    top:0;
    left:0;
    >.myOrderListHeaderLeft{
       width:0.32rem;
        height:0.56rem;
    }
    >span{
        font-size:0.53rem;
    }
    >.myOrderListHeaderRight{
        width:0.43rem;
        height:0.45rem;
    }
    
}
// 
.promptFather{
    width:100%;
    position :absolute;
    bottom:50%;
    left:0;
    z-index:999;
    display :flex;
    justify-content :center;
    align-items :center;
    >.prompt{
        padding:0.15rem 0.3rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.32rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}

.comOrder_body{
   width:9.2rem;
    padding:0.32rem;
    margin:0 auto;
    box-shadow :0 0.05rem 0.3rem rgba(255, 204, 212, 0.5);
    >.comOrder_bodyTitle{
        font-size:0.4rem;
    }
    >.comOrder_bodyDes{
        display :flex;
        justify-content :space-between;
        align-items :center;
        font-size:0.35rem;
        margin-top:0.32rem;
        >img{
            width:0.43rem;
            height:0.43rem;
        }
        >.comOrder_bodyDesTop{
            >img{
                width:0.43rem;
                height:0.43rem;
                margin-right:0.32rem;
            }
        }
    }
}
.comOrder_info{
    width:9.2rem;
    padding:0.32rem;
    font-size:0.347rem;
    margin:0.32rem auto;
    box-shadow :0 0.05rem 0.3rem rgba(255, 204, 212, 0.5);
    >.comOrder_infoTitle{
        font-size:0.43rem;
        color:rgba(0, 0, 0, 1);
        margin-bottom:0.4rem;
    }
    >.comOrder_infoMoney{
        margin-top:0.32rem;
        >span{
            color:rgba(236, 10, 66, 1);
        }
    }
}
</style>


