<!--  -->
<template>
  <div class="shopDetail">
        <!-- header -->
            <div class="shopDetail_header">
                <img class="img_left" @click.stop="toReturn" :src="staticImgH+'zuojiantou.png'" alt="">
                <span></span>
                <img @click="shareC" class="img_right" :src="staticImgH+'shopDetailShare.png'" alt="">
            </div>
        <!--  -->
            <div class="shopDetail_con">
                <div class="KcodeShopHeader">
                        <!--  -->
                        <swiper class="KcodeShopSwiper"  :options="swiperOption">
                            <swiper-slide class="KcodeShopSwiperList"  v-for="(item,index) in galleryImg" :key="index">
                                <img :src="item.image_url" alt=""></swiper-slide>
                            <div class="swiper-pagination KcodeShopPage" slot="pagination"></div>
                        </swiper>
                </div>
            </div>
        <!--   -->
            <div class="shop_detail">
                    <div class="shop_detailTop"><span>{{shopData.shop_price}}</span> <span class="originalPrice">{{shopData.market_price}}</span></div>
                    <div class="shop_detailCenter">{{shopData.goods_vote}}</div>
                    <div class="shop_detailBottom">{{shopData.goods_name}}</div>
            </div>
        <!--  -->
            <div class="shopContent" v-html="shopData.goods_content"></div>
        <!--   -->
            <div class="shopDetail_btnWrap">
                <div class="shopDetail_btnone" @click="toPlayStyle">
                     
                </div>
                <div  class="shopDetail_btnTwo" @click="toHome">
                     
                </div>
                <div  class="shopDetail_btn" @click="AssistTicketOne">
                     
                </div>
                    
            </div>
        <!--   -->
            <div class="shopDetail_hide"> 
            </div>
        <!--  -->
            <transition name="fade">
                <div class="promptFather" v-if="showPrompt">
                    <div class="prompt" >
                        {{promptContent}}
                    </div>
                </div>
            </transition>
        <!--  -->
            <div class="shareText_wrap" v-if="shareTextShow"  @click="hideShare">
                <img :src="staticImgH+'timg.jpg'"/>
                <div class="shareText">
                    ta
                </div> 
            </div>
        <!--  -->
            <div class="orderTIshi_wrap" @click="sureOrderTi" v-if="orderTIshiSHow">
                <div class="orderTIshi" @click.stop>
                    <span class="orderTIshi_des"></span>
                    <span class="orderTIshi_btn" @click="sureOrderTi"></span>
                </div>
            </div>
  </div>
</template>
<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import wx from 'weixin-jsapi'
import qs from 'qs'
export default {
  data () {
    return {
        galleryImg:'',//
        shopData:'',//
        // 
        swiperOption: {
            pagination: {
                el: '.swiper-pagination',
                type: 'fraction'
            },
            autoplay: {
                delay: 1500,
                stopOnLastSlide: false,
                disableOnInteraction: false
            },
            loop: true,
        },
        // 
        promptContent:'', //
        showPrompt:false,//
        timer2:'',

        BshopId:'',//ID
        BuserIdH:"",//ID\

        // 
        unionid:'',  
        miniapp:'',
        nickname:'',
        head_pic:'',
        sex:'',
        test:'',
        shareTextShow:false,//
        orderTIshiSHow:false,//

        dataResult:'',
    };
  },
  components: {},
  computed:{
        ...mapState(['apiH','staticImgH','tokenH','shopgoodId','shopDetailReturn','playerId','addressId','addressIdIsSel','userIdH','WpersonInfoH','userIdHInterest'])
    },
  mounted(){
      this.firstPanduan()
  },
  methods: {
           //   
                   sureOrderTi(){
                       this.orderTIshiSHow=false
                   },
            //   
                    shareC(){
                        this.shareTextShow=true
                    },
            //   
                    hideShare(){
                        this.shareTextShow=false
                    },
            //
            toHome(){
                this.$router.push('/')
            },
            // 
            toPlayStyle(){
                this.$router.push('/PlayerStyle')
            },
            firstPanduan(){
                //
                    var shopUrl = window.location.href
                        //var shopUrl = 'http://mobile.aibebi.cn/aibei/shopList.html?goods_id=1482'
                        //id
                        var shopCan, value;  // 
                        shopCan = shopUrl.indexOf("#")  //
                        shopUrl=shopUrl.substr(shopCan+1)
                        shopCan=shopUrl.indexOf('?')
                        if(shopCan > 0){
                            shopUrl = shopUrl.substr(shopCan + 1)  //
                            var shopArr = shopUrl.split('&') // 
                            var shopUrlId = {};//  goodId
                            for(var i = 0; i < shopArr.length; i++) {//shopArr
                                shopCan = shopArr[i].indexOf("=");  //=
                                if(shopCan > 0){ //=
                                    value = shopArr[i].substring(shopCan + 1); //=     =>value
                                    shopCan = shopArr[i].substring(0, shopCan);//=  =key
                                    shopUrlId[shopCan] = value;  // key valueshopUrlId
                                }
                            }
                            if(shopUrlId.goods_id){
                                this.BshopId=shopUrlId.goods_id
                            }else{
                                this.BshopId=this.shopgoodId
                            }
                        }else{
                                this.BuserIdH=this.userIdH
                                this.BshopId=this.shopgoodId
                        }
                // 
                    var obj=qs.stringify({
                            goods_id:this.BshopId
                        })
                        this.$http.post('/api/goods/goods_info',obj).then((res)=>{
                            if(res.data.code==200){
                                this.shopData=res.data.data
                                this.galleryImg=res.data.data.image_url
                                //
                                if(this.addressIdIsSel=='true'){
                                    this.AssistTicketTwo()
                                }
                                this.WShare()
                            }else{
                                var self=this
                                clearInterval(self.timer2);
                                this.promptContent=res.data.msg
                                this.showPrompt=true
                                self.timer2=setTimeout(function(){
                                    self.showPrompt=false
                                    clearInterval(self.timer2);
                                },1000)
                                return false;
                            }
                        })
                
                    
            },
        // 
            WShare(){
                var Wobj=qs.stringify({
                    player_id:this.userIdH,
                    type:1,
                })
                this.$http.post('/api/wechat/get_sign',Wobj).then((res)=>{
                    if(res.data.code==200){
                        var data=res.data.data
                        this.test=data.test
                            wx.config({
                                debug: false, // ,apialertpclogpc
                                appId: data.appId, // 
                                timestamp:data.timestamp, // 
                                nonceStr: data.nonceStr, // 
                                signature: data.signature,// 
                                jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // JS
                            });
                            this.toFriend()
                            this.toFriendQuan()
                    }else{
                            var self=this
                            clearInterval(self.timer2);
                            this.promptContent=res.data.msg
                            this.showPrompt=true
                            self.timer2=setTimeout(function(){
                                self.showPrompt=false
                                clearInterval(self.timer2);
                            },1000)
                            return false;
                    }
                })
            },
        //   
            toFriend(){
                var vm=this
                var realLocation=vm.apiH+'/#/shopDetails?goods_id='+vm.BshopId
                wx.ready(function () {   //
                    wx.onMenuShareAppMessage({ 
                        title:vm.test, // 
                        desc:' ', // 
                        link:vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation), // JS
                        imgUrl: vm.shopData.original_img, // 
                        success: function (res) {
                                            var GetVotesData=qs.stringify({
                                                type:3,
                                                amount:1
                                            })
                                            vm.$http.post('api/user/get_votes',GetVotesData).then((resTwo)=>{
                                                if(res.data.code!==200){
                                                    alert('yes')
                                                    clearInterval(vm.timer2);
                                                    vm.promptContent=resTwo.data.msg
                                                    vm.showPrompt=true
                                                    vm.timer2=setTimeout(function(){
                                                        vm.showPrompt=false
                                                        clearInterval(vm.timer2);
                                                    },2000)
                                                    return false;
                                                }
                                            })
                        }
                    })
                });
            },
        //   
            toFriendQuan(){
                    var vm=this
                    var realLocation=vm.apiH+'/#/shopDetails?goods_id='+vm.BshopId 
                    wx.ready(function () {   //
                        wx.onMenuShareTimeline({
                                title:vm.test, // 
                                link: vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation),  // JS
                                imgUrl:vm.shopData.original_img, // 
                                success: function (res) {
                                            var GetVotesData=qs.stringify({
                                                type:3,
                                                amount:1
                                            })
                                            vm.$http.post('api/user/get_votes',GetVotesData).then((resTwo)=>{
                                                if(res.data.code!==200){
                                                    alert('yes')
                                                    clearInterval(vm.timer2);
                                                    vm.promptContent=resTwo.data.msg
                                                    vm.showPrompt=true
                                                    vm.timer2=setTimeout(function(){
                                                        vm.showPrompt=false
                                                        clearInterval(vm.timer2);
                                                    },2000)
                                                    return false;
                                                }
                                            })
                                },
                        })
                });
            },
        //   
            toReturn(){
                if(this.shopDetailReturn=="/PlayerDetails"){
                        this.playerIds(this.playerId)//id
                        this.playDetailVoteDivs('true') //
                        this.addressIdIsSels('false') // 
                        this.shopDetatilshows('true')//
                        this.$router.push('/PlayerDetails')
                }else if(this.shopDetailReturn==''){
                    this.$router.push('/')
                }else{
                    this.$router.push(this.shopDetailReturn)
                }
            },
        //   
            AssistTicketOne(){
                this.addressIdIsSels('false')
                this.AssistTicket()
            },
        //   
            AssistTicket(){
                    if(this.addressIdIsSel=='false'){   // addressIdIsSelfalse 
                            this.$http.post("api/user/address_list").then((res)=>{
                                this.shopgoodIds(this.BshopId)  //gooid
                                if(res.data.data.length){  //
                                    this.ReceiptAddressPages('/shopDetails')
                                    this.$router.push('/ReceiptAddress')
                                }else{  //
                                    this.ReceiptAddressAddPages('/shopDetails')
                                    this.$router.push('/ReceiptAddressAdd')
                                }
                            })
                        }else if(this.addressIdIsSel=='true'){  //
                                this.AssistTicketTwo()
                        }
            },
        // ()
            AssistTicketTwo(){
                var obj=qs.stringify({
                    goods_id:this.shopgoodId,
                    player_id:this.userIdH,
                    address_id:this.addressId
                })
                this.$http.post('api/goods/goods_buyer',obj).then((res)=>{
                    if(res.data.code==200){
                        this.orderSn=res.data.data.result  //
                        this.getPayPermit()
                    }else{
                        var self=this
                            clearInterval(self.timer2);
                            this.promptContent=res.data.msg
                            this.showPrompt=true
                            self.timer2=setTimeout(function(){
                                self.showPrompt=false
                                clearInterval(self.timer2);
                            },2000)
                            return false;
                    }
                })
            },
        //    wx.config
            getPayPermit(){
                var PayPermitObj=qs.stringify({
                    order_sn:this.orderSn,
                    trade_type:'JSAPI',
                })
                this.$http.post('api/wechat/dopay',PayPermitObj).then((res)=>{
                        if(res.data.code==200){
                            var data=res.data.data
                            this.dataResult=data //
                            var vm=this
                            wx.config({
                                debug: false, // ,apialertpclogpc
                                appId: data.appId, // 
                                timestamp:data.timeStamp, // 
                                nonceStr: data.nonceStr, // 
                                signature: data.sign,// 
                                jsApiList: ['chooseWXPay'] // JS
                            });
                            this.payLe()
                        }else{
                            var self=this
                            clearInterval(self.timer2);
                                    this.promptContent=res.data.msg
                                    this.showPrompt=true
                                    self.timer2=setTimeout(function(){
                                        self.showPrompt=false
                                        clearInterval(self.timer2);
                                    },2000)
                                return false;
                        }
                })
            },
        //    wx.chooseWXPay
            payLe(){
                    this.addressIdIsSels('false')
                    var vm=this
                    wx.ready(function(){
                        wx.chooseWXPay({
                            timestamp: vm.dataResult.timeStamp, // jssdktimestamptimeStampS
                            nonceStr: vm.dataResult.nonceStr, //  32 
                            package: vm.dataResult.packag, // prepay_idprepay_id=\*\*\*
                            signType: 'MD5', // 'SHA1''MD5'
                            paySign:  vm.dataResult.sign, // 
                            success: function (res) {
                                vm.addressIdIsSels('false')
                                // vm.myOrderListPages('/shopDetail')  //
                                // vm.orderTypes('WAITSEND')  //orderType
                                // vm.orderNums(2)   //
                                // vm.$router.push('/orderList')  //
                                vm.orderTIshiSHow=true
                            },
                            fail(){
                                alert('')
                            }
                        });
                    }) 
            },
        // 
            // Islogin(){
            //     var LoginObj=qs.stringify({
            //         unionid:this.unionid,
            //         miniapp:this.miniapp,
            //         nickname:this.nickname,
            //         head_pic:this.head_pic,
            //         sex:this.sex
            //     })
            //     this.$http.post('api/user/logins',LoginObj).then((res)=>{
            //         if(res.data.code==200){
            //             this.userIdHs(res.data.data.result.user_id)
            //             this.tokenHs(res.data.data.result.token)
            //             this.firstPanduan()
            //         }else{
            //             var self=this
            //             clearInterval(self.timer2);
            //                     this.promptContent=res.data.msg
            //                     this.showPrompt=true
            //                     self.timer2=setTimeout(function(){
            //                         self.showPrompt=false
            //                         clearInterval(self.timer2);
            //                     },2000)
            //                 return false;
            //             this.getCodes ()  //code!==200code
            //         }
            //     })
            // },
            //code
            // getUrlParam(name) {//
            //     var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //
            //     var r = window.location.search.substr(1).match(reg); //
            //     if (r != null) return unescape(r[2]);
            //     return null; //
            // },
            // 
            // getCodes () {
            //     const AppId='wx7cb8b9da4ff3bac2';
            //     const code=this.getUrlParam('code')
            //     const local=window.location.href
            //     if(code==null || code===''){
            //         window.location.href='https://open.weixin.qq.com/connect/oauth2/authorize?appid='+AppId+'&redirect_uri='+encodeURIComponent(local)+'&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
            //     }else{
            //         var obj=qs.stringify({
            //         code:code
            //         })
            //         this.$http.post('/api/user/user_login',obj).then((res)=>{
            //             if(res.data.code==200){
            //                 var data=res.data.data
            //                 let personInfo={unionid:data.unionid,miniapp:data.openid,nickname:data.nickname,head_pic:data.headimgurl,sex:data.sex}
            //                 this.WpersonInfoHs(personInfo)
            //                 this.unionid=data.unionid
            //                 this.miniapp=data.openid
            //                 this.nickname=data.nickname
            //                 this.head_pic=data.headimgurl
            //                 this.sex=data.sex
            //                 this.Islogin()  
            //             }else{
            //                 var self=this
            //                 clearInterval(self.timer2);
            //                         this.promptContent=res.data.msg
            //                         this.showPrompt=true
            //                         self.timer2=setTimeout(function(){
            //                             self.showPrompt=false
            //                             clearInterval(self.timer2);
            //                         },2000)
            //                     return false;
            //             }
            //         })
            //     }
            // },
          ...mapMutations(['myOrderListPages','orderTypes','orderList','ReceiptAddressPages','ReceiptAddressAddPages','playerIds','addressIdIsSels','playDetailVoteDivs','shopDetatilshows','WpersonInfoHs','tokenHs','shopgoodIds','userIdHs','userIdHInterests'])
    },
   
}

</script>
<style scoped lang="stylus">
.shopDetail_header{
    width:100%
    height:1.23rem;
    display :flex;
    align-items :center;
    justify-content :space-between;
    padding:0 0.53rem;
    z-index:999;
    >.img_left{
         width:0.32rem;
        height:0.56rem;
    }
    >span{
        font-size:0.48rem;
    }
    >.img_right{
        width:0.53rem;
        height:0.53rem;
    }
}
.KcodeShopHeader>.KcodeShopSwiper{
    width:100%;
    height:9.7rem;
    position :relative;
    .KcodeShopSwiperList{
        >img{
            width:100%;
            height:9.7rem;
        }
    }
}
.KcodeShopHeader>.KcodeShopSwiper>.KcodeShopPage{
    width:1.07rem;
    height:0.53rem;
    font-size:0.32rem;
    background :rgba(0,0,0,0.5);
    line-height: 0.53rem;
    text-align :center;
    border-radius:0.35rem;
    color :#fff;
    position :absolute;
    left:8.53rem;
    bottom:0.27rem;
}
.shop_detail{
    background:#fff;
    padding:0.4rem 0.53rem;
    margin-bottom:0.32rem;
    >.shop_detailTop{
        font-size:0.53rem;
        color:rgba(236, 10, 66, 1);
        display:flex;
        >.originalPrice{
            margin-left:0.4rem;
            text-decoration: line-through;
            color:rgba(0, 0, 0, 0.36);
        }
    }
    >.shop_detailCenter{
        color:rgba(255, 157, 172, 1);
        font-size:0.32rem;
        margin-bottom:0.32rem;
        margin-top:0.373rem;
    }
    >.shop_detailBottom{
        font-size:0.48rem;
        color:rgba(0, 0, 0, 0.8);
        line-height:0.667rem;
    }
}
.shopDetail_btnWrap{
    width:100%;
    height:1.2rem;
    position:fixed;
    bottom:0;
    left:0;
    display:flex;
}
.shopDetail_btnone{
    width:33.333%;
    height:1.2rem;
    background:#FED943;
    font-size:0.37rem;
    color:rgba(255, 255, 255, 1);
    text-align:center;
    line-height:1.2rem;
}
.shopDetail_btnTwo{
    width:33.333%;
    height:1.2rem;
    background:#4AABF9;
    font-size:0.37rem;
    color:rgba(255, 255, 255, 1);
    text-align:center;
    line-height:1.2rem;
}
.shopDetail_btn{
    width:33.333%;
    height:1.2rem;
    background:rgba(255, 157, 172, 1);
    font-size:0.37rem;
    color:rgba(255, 255, 255, 1);
    text-align:center;
    line-height:1.2rem;
}
.shopDetail_hide{
     width:9.2rem;
   height:1.2rem;
}
.shopContent{
    width:100%;

    >>>p{
        width:100%;
        >img{
            width:100%;
        }
    }
}

// 
.promptFather{
    width:100%;
    position :absolute;
    bottom:50%;
    left:0;
    z-index:10;
    display :flex;
    justify-content :center;
    align-items :center;
    >.prompt{
        padding:0.15rem 0.3rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.32rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}

.shareText_wrap{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.9);
    position:fixed;
    top:0;
    left:0;
    z-index:999;
    >img{
        width:2.51rem;
        height:auto;
        position:absolute;
        right:0.32rem;
        top:0.32rem;
    }
    >.shareText{
        width:7.733rem;
        height:5.01rem;
        position:absolute;
        left:0;
        right:0;
        top:0;
        bottom:0;
        margin:auto;
        font-size:0.48rem;
        text-align:center;
        padding:0.4rem;
        line-height:1rem;
        color:#fff;
    }
}
// 
.orderTIshi_wrap{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.9);
    position:fixed;
    left:0;
    top:0;
    z-index:999;
   
    >.orderTIshi{
        width:8rem;
        height:3.84rem;
        background:#fff;
        border-radius:0.133rem;
         display:flex;
        flex-direction:column;
        position:absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        margin:auto;
        >.orderTIshi_des{
            width:100%;
            height:3rem;
            font-size:0.373rem;
            color:rgba(0, 0, 0, 1);
            line-height:0.8rem;
            text-align:center;
            padding:0.4rem;
        }
        >.orderTIshi_btn{
            width:100%;
            height:1.253rem;
            font-size:0.373rem;
            color:rgba(255, 57, 36, 1);
            text-align:center;
            line-height:1.253rem;
            border-top:0.03rem solid rgba(204, 204, 204, 0.4);
        }
    }
}
</style>


